.more-skills{
  font-family: Verdana sans-serif;
font-stretch: expanded;
padding: 5% 0 0 0;
}
.list {
    padding:0;
    list-style: none;
}

.list li {
   color: #506a6b;
}
.list li:before {
    padding-right:10px;
    font-weight: bold;
    color: #C0C0C0;
    content: "\2714";
    transition-duration: 0.3s;
}
.list li:hover:before {
    color: #2f2f2f;
    content: "\2714";
}
.list li:hover{
  color: #2f2f2f;
}
