.logo {
	display: flex;
	justify-content:center;
  align-items:center;
  flex-flow: row wrap;
  border: 1px solid black;
	width: 100%;
	padding-top:4%;
	padding-bottom: 14%;
	margin-top: 0.5%
}
.scale:hover{
transform: scale(1.01);
transition: all 0.3s ease;
box-shadow: 0px 0px 15px 7px rgba(0, 0, 0, 0.1);
}
.logo-image {
	width: 90px;
	height: 90px;
	border: 1px solid #39383a;
	border-radius: 50%;
	display: flex;
	justify-content: center;
	align-items: center;
}

.logo-lines {
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	margin: 0 5%;
}

.logo-line {
	width: 43px;
	height: 2px;
	background-color: #39383a;
	margin-top: 10px;
	margin-bottom: 10px;
}

.logo-lines__left .logo-line:nth-child(2) {
	width: 55px;
	margin-right: 22%;

}

.logo-lines__right .logo-line:nth-child(2) {
	width: 55px;
	margin-left: 22%;

}

.name {
	font-size: 16px;
	text-transform: uppercase;
	letter-spacing: 10.5px;
	margin-bottom: 10px;
}

.about-address {
	font-size: 13px;
	margin-bottom: 15px;
}

.about-contacts {
	font-size: 10px;
}

.about-contacts__link {
	text-decoration: none;
	color: #777777;
}
.center{
	flex-wrap: wrap;
	text-align: center;
	padding-left: 10%;
padding-right: 10%;
text-wrap:nowrap;
}
